body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
    background-image: url('https://img.rawpixel.com/s3fs-private/rawpixel_images/website_content/v936-background-adj-08.jpg?w=800&dpr=1&fit=default&crop=default&q=65&vib=3&con=3&usm=15&bg=F4F4F3&ixlib=js-2.2.1&s=eb5816bfb1a2e622f0fe22c39376e217');
    background-repeat: no-repeat;
    background-size: cover;
  }
  
  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    font-size: 36px;
    margin-bottom: 10px;
    color: tomato;
  }
  
  .square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 48px;
    font-weight: bold;
    line-height: 34px;
    height: 90px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 90px;
  }

  .square > img {
    width: 100%;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: row;
    margin-top: 40px;
    margin-left: 130px;
  }
  
  .game-info {
    margin-left: 50px;
  }

  .quiz {
    margin-top: 20px;
    font-size: 24px;
  }

  .choice {
    margin-top: 10px;
    font-size: 24px;
    width: 500px;
  }

  .game-info {
    margin-left: 350px;
  }

  .questionHide {
    display: none;
    transition: display 4s;
  }

  .questionShow {
    display: block;
    transition: display 4s;
  }

  .carousel-root {
    margin: 0 auto;
    margin-top: 50px;
    width: 50%;
  }

  .audio {
    position: fixed;
    bottom: 25px;
  }

  